.contacts {

  &__title {
    margin-bottom: 48px;
  }

  &__wrapper {
    @include pageGrid;
    
    row-gap: 48px;
  }

  &__text-block--top {
    grid-column: 1 / -1;

    @include onTable {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 4;
    }
  }

  &__text-block--bottom {
     grid-column: 1 / -1;

    @include onTable {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 4 / 7;
    }
  }

  &__sub-title {
    margin-bottom: 16px;
  }

  &__tell-link {
    display: block;
    margin-bottom: 8px;

    @include hover(color, $red);
  }

  &__adress {
    @include hover(color, $red);
  }
}
