.explore-online {
  &__title {
    margin-bottom: 40px;
  }

  &__wrapper {
    @include pageGrid;

    row-gap: 48px;
    justify-content: center;
  }

  &__block {
    overflow: hidden;
    grid-column: span 2;
    padding-bottom: 5px;

    @include hover(box-shadow, 0px 8px 16px rgba(0, 0, 0, 0.2));
    @include hover(border-radius, 10px);
    @include hover(transform, scale(1.03));

    @include onTable {
      grid-column: 2 / -2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }

  &__image {
    width: 100%;
    display: block;
  }

  &__title-h3 {
    margin: 32px 0 5px;
    text-align: center;
    color: $red;
  }

  &__text {
    font-family: Roboto, sans-serif;
    text-align: center;
  }
}
